@import '../../../../index.scss';

.mobileGrid {
    display: grid;
    grid-template-columns: 91vw 9vw;
    z-index: 1;
}

.mobileGrid__last {
    display: grid;
    grid-template-columns: 9vw 91vw;
    z-index: 1;
}

.screen {
    z-index: 1;
    display: flex;
}

.nextButton, .nextButton button {
    width: 100%;
    height: 100%;
    background: transparent;
    box-shadow: none;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;

    &:focus {
        outline: none;
    }
}